:root {
--primary-color: #6c757d;
--secondary-color: #343a40;
--border-color: var(--primary-color);
}

body {
overflow-x: hidden; /* add this property to prevent horizontal scrolling */
margin: 0;
padding: 0;
font-family: sans-serif;
background: #bcc1c6;
font-weight: 800;
}

#search_form_form {
background-color: var(--secondary-color);
padding: 1em;
display: flex;
align-items: center;
flex-direction: column;
}

#search_form_form > label {
color: white;
font-size: 2em;
margin-top: .2em;
margin-bottom: 20px;
}

#search_form_form input {
width: 76%;
margin: 0em 20%;
height: 20px;
border: 0;
border-radius: 5px;
padding: .4em 20px;
font-size: 1.2em;

color: var(--secondary-color);
background-color: var(--primary-color);
}

#search_form_form input.populated {
background-color: var(--secondary-color);
color: var(--primary-color);

border: var(--border-color) 1px solid;

transition: none 1s ease-in-out;
transition-property: background-color,color,border-color;
}

#search_form_form input.not_populated {
background-color: var(--primary-color);
color: var(--secondary-color);

border: var(--secondary-color) 1px solid;

transition: none .3s ease-in-out;
transition-property: background-color,color,border-color;
}

#search_form_form input.not_populated::placeholder {
    color: var(--secondary-color);
}

#search_form_radio_buttons {
display: flex;
flex-direction: row;
justify-content: space-between;
width: 80%;
margin: 0em 20%;
padding: 0em 0em 1em 0em;
}

.radio_button {
display: flex;
justify-content: center;
align-items: center;
background-color: var(--primary-color);
border: var(--primary-color) 1px solid;

cursor: pointer;

transition: none .3s ease-in-out;
transition-property: background-color,color,border-color;
}

.radio_button input[type="radio"] {
display: none;
}

.radio_button:hover {
color: var(--secondary-color);
background-color: var(--primary-color);

border: var(--secondary-color) 1px solid;
}

.radio_button label {
padding: .2em 2em; /* was 6em */
cursor: pointer;
}

.radio_button input[type="radio"]:checked + label {
background-color: var(--secondary-color);
color: var(--primary-color);

border: var(--primary-color) 1px solid;
transition: none .18s ease-in-out;
transition-property: background-color,color,border-color;
}

#submit_button {
font-size: 1.2em;
font-weight: inherit;
border: var(--secondary-color) 1px solid;
border-radius: 5px;
padding: .4em 10%;
width: 80%;
background: var(--primary-color);
color: var(--secondary-color);
cursor: pointer;
transition: none .18s ease-in-out;
transition-property: background-color,color,border-color;
}

#submit_button:hover {
color: var(--primary-color);
background-color: var(--secondary-color);

border: var(--primary-color) 1px solid;
}

.loader {
border: 10px solid var(--secondary-color);;
border-top: 10px solid var(--primary-color);;
border-radius: 50%;
width: 60px;
height: 60px;
animation: spin 2s linear infinite;
margin: 2em auto 0em;
}

@keyframes spin {
0% { transform: rotate(0deg); }
100% { transform: rotate(360deg); }
}

#results_unordered_list {
display: flex;
flex-wrap: wrap;
justify-content: space-between;
padding: 0;
margin: 2em auto 0em;
list-style-type: none;
}

.result_list_item {
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
width: 100%;
margin-bottom: 30px;
padding: 0em .6em;
box-sizing: border-box;

cursor: pointer;
}

.result_img {
border: 1px solid #030303; /* border color and width */
border-radius: 5px; /* make corners rounded */
}

@media screen and (min-width: 600px) {
.result_list_item {
    width: calc(50% - 10px);
}
}

@media screen and (min-width: 900px) {
.result_list_item {
    width: calc(33.33% - 10px);
}
}

/* Adjustments for mobile responsiveness */
@media screen and (max-width: 600px) {
#search_form_form {
    padding: 1em;
}

#search_form_form input {
    width: 90%;
    margin: 0 5%;
}

#search_form_form > label {
    font-size: 1em;
}

#search_form_radio_buttons {
    flex-direction: column;
    align-items: center;
}

.radio_button {
    margin: .2em 0;
}

.radio_button label {
    padding: .2em 2em;
}

#submit_button {
    width: 90%;
}
}
  

.error_banner {
    color: white;
    background-color: black;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;

    padding: 20px;
    position: relative;
    box-sizing: border-box;

    /* Create a border with a yellow spaced strip */
    background-image: 
        linear-gradient(to right, red 50%, transparent 50%),
        linear-gradient(to bottom, red 50%, transparent 50%),
        linear-gradient(to left, red 50%, transparent 50%),
        linear-gradient(to top, red 50%, transparent 50%);
    background-size: 10px 2px, 2px 10px;
    background-repeat: repeat-x, repeat-y;
    background-position: top, right, bottom, left;
}


